<template>
    <div class="cart-control full-width">
        <div class="container bottom-bar">
            <label for="SelectAll"
                   style="margin-right: 20px;
                        user-select: none"
                    class="cursor">
                <input id="SelectAll" type="checkbox" v-model="selectAll">
                全选
            </label>
            <a @click="delCartSelect"
               style="margin-right: 20px">
                删除选中的商品
            </a>
            <a @click="AllAddCollect"
               style="margin-right: 20px">
                移入收藏夹
            </a>
            <div class="fr bill ac bold cursor" @click="Order">结&nbsp;&nbsp;算</div>
            <div class="fr mr-20"><span>合计：</span><span class="price theme-color bold">{{TotalPrice}} <span style="font-size: 14px;color: black">{{storage.getStorage('exchange').unit}}</span></span></div>
            <div class="fr mr-20">
                <span>已选商品：</span>
                <span class="theme-color bold ">{{Total}}</span>
                <span>件</span>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    export default {
        name: 'cart-control',
        data() {
            return {}
        },
        computed:{
            ...mapState('CART',['shopping_cart','cart_length']),
            selectAll:{
                get(){
                    if (this.shopping_cart.length === this.cart_length){
                        return true
                    } else{
                        return false
                    }
                },
                set(e){
                    this.bus.$emit('SelectAll',e)
                }
            },
            TotalPrice(){
                let cache = [];
                if (this.shopping_cart.length>0){
                    let exchange = parseInt(this.storage.getStorage('exchange').exchange);
                    this.shopping_cart.forEach(item=>{
                        cache.push(item.jifen*item.nums/exchange)
                    });
                    return this.Sum(cache)
                }else{
                    return 0
                }
            },
            Total(){
                let cache = [];
                if (this.shopping_cart.length>0){
                    this.shopping_cart.forEach(item=>{
                        cache.push(item.nums)
                    });
                    return this.Sum(cache)
                }else{
                    return 0
                }
            }
        },
        methods:{
            Sum(arr) {
                let s = 0;
                arr.forEach(function(val, idx, arr) {
                    s += val;
                }, 0);
                return Math.round(s*100)/100;
            },
            Order(){
                if(this.shopping_cart.length>0){
                    this.$router.push({
                        path:'/order/Settlement'
                    })
                }else{
                    this.bus.$emit('notice','您还没选择需要结算的商品~')
                }
            },
            delCartSelect(){
                let data = {product_id:[]};
                this.shopping_cart.forEach(item=>{
                    data.product_id.push(item.product_id)
                })
                this.yum('api/member/delCart',data).then(res=>{
                    if (res.data.status === 200 ){
                        this.$router.go(0)
                    }
                })
            },
            AllAddCollect(){
                let data = {product_id:[]};
                this.shopping_cart.forEach(item=>{
                    data.product_id.push(item.product_id)
                })
                this.yum('api/member/collect',data).then(res=>{
                    this.bus.$emit('notice',res.data.msg)
                })
            }
        }
    }
</script>

<style lang="stylus" scoped>
.full-width
    min-width 1000px
.cart-control
    position fixed
    z-index 999
    bottom 0
    left 0
    height 60px
    line-height 60px
    background-color #f2f2f2
    .bottom-bar
        position relative
        padding-left 25px
        .bill
            width 100px
            background-color #d7d7d7
            color black
            font-size 16px
            &:hover
                background-color #ff0077
                color white
        .mr-20
            margin-right 20px
        .price
            font-size 20px
            line-height 19px
</style>
