/**
 * 我的订单页面状态管理
 *
 *
 * */
let today = new Date();
let year = today.getFullYear();
let month = today.getMonth()+1;
let date = today.getDate();
export const TOBEPAID = 'TOBEPAID';
export const DELETEPAID = 'DELETEPAID';
export const DELETEALL = 'DELETEALL';
export const SETSTART = 'SETSTART';
export const SETEND = 'SETEND';
export const state = () => {
    return {
        ToBePaid: [],
        start_time: '2018-01-01',
        end_time: `${year}-${month}-${date>9?date:'0'+date}`
    }
};
export const mutations = {
    /**
     * 操作添加，删除选中的订单
     * */
    [TOBEPAID](state, res) {
        if (state.ToBePaid.indexOf(res) === -1) {
            state.ToBePaid.push(res)
        }
    },
    [DELETEPAID](state, update) {
        state.ToBePaid.splice(state.ToBePaid.indexOf(update), 1)
    },
    [DELETEALL](state) {
        state.ToBePaid = []
    },
    [SETSTART](state, update) {
        state.start_time = update
    },
    [SETEND](state, update) {
        state.end_time = update
    }
};
export const actions = {
    /**
     * 暴露出来 供外部访问的 添加，删除方法
     * */
    Insert({commit}, update) {
        commit(TOBEPAID, update)
    },
    Delete({commit}, update) {
        commit(DELETEPAID, update)
    },
    DeleteAll({commit}) {
        commit(DELETEALL)
    },
    setStart({commit}, update) {
        commit(SETSTART, update)
    },
    setEnd({commit}, update) {
        commit(SETEND, update)
    }
};