import Vue from 'vue'
import App from './app.vue'
import MetaInfo from 'vue-meta-info'
import storage from '../../components/storage'
import yum from '../../components/yum'
import '../../assets/css/public.css'
import './assets/style/public.styl'
import router from './router'
import {createStore} from './vue.state'
import bus from '../../components/event-bus'
import axios from 'axios'
import { DatePicker } from 'element-ui'
Vue.use(DatePicker);
Vue.prototype.axios = axios;
Vue.prototype.bus = bus;
const store = createStore;
Vue.prototype.storage = storage;
Vue.prototype.yum = yum;
Vue.use(MetaInfo);
Vue.filter('img',  function ($url){
    if(/^http/.test($url)){
        return $url.replace('com/n0', 'com/img');
    }else if(/^\/.{4}\//.test($url)){
        // /2017/12/25/5a40b39bc9819.jpg
        return 'http://admin.fufentong.com/media/upload'+$url;
    }else{
        // /tmp/uploads/20181127/4410c3daa57707828e7e242aa97395fa.png
        return 'http://mapi.fufentong.com'+$url;
    }
});

function add0(m) {
    return m < 10 ? '0' + m : m
}

Vue.filter('format', function (item) {
    var time = new Date(item * 1000);
    var y = time.getFullYear();
    var m = time.getMonth() + 1;
    var d = time.getDate();
    var h = time.getHours();
    var mm = time.getMinutes();
    var s = time.getSeconds();
    return y + '-' + add0(m) + '-' + add0(d) + ' ' + add0(h) + ':' + add0(mm) + ':' + add0(s);
})
Vue.filter('img1',  function ($url){
    if(/^http/.test($url)){
        return $url.replace('com/n0', 'com/img');
    }else if(/^\/.{4}\//.test($url)){
        // /2017/12/25/5a40b39bc9819.jpg
        return 'http://master.fufentong.com/media/upload'+$url;
    }else{
        // /tmp/uploads/20181127/4410c3daa57707828e7e242aa97395fa.png
        return 'http://mapi.fufentong.com'+$url;
    }
});
Vue.filter('img_mapi', function (item) {
    let testUrl = /http:\/\//i;
    if (testUrl.test(item)) {
        return item
    } else {
        return `http://mapi.fufentong.com/${item}`
    }
});
Vue.filter('exch', function (item, ex) {
    let jifen = item.jifen;
    let exchange = ex.exchange;
    let num = parseFloat(jifen) / parseFloat(exchange);
    let show = (Math.round(num * 100) / 100);
    return exchange == 10 ? show.toFixed(2) : show.toFixed(2)
});
Vue.filter('increase', function (item, type) {
    if (type === 1) {
        return `+${item}`
    } else {
        return `-${item}`
    }
});
// 单位换算
Vue.filter('change_unit', function (x) {
    var eu = storage.getStorage('exchange');
    let ret = parseFloat(x) / parseFloat(eu.exchange);
    let rets = (Math.round(ret * 100) / 100);
    return eu.exchange == 10 ? rets.toFixed(2) : rets
});

/**
 * 订单状态过滤器
 */
Vue.filter('order_status', function (status, pay_status, shipping_status) {
    /* console.log(status);
     console.log(pay_status);*/
    if (pay_status == 0 && status != 2) {
        return '未付款';
    }
    else if (status == 1 && shipping_status == 0 && pay_status == 1) {
        return '待发货';
    } else if (status == 1 && shipping_status == 2 && pay_status == 1) {
        return '已发货';
    } else if (status == 1 && shipping_status == 3 && pay_status == 1) {
        return '已收货';
    } else if (status == 5 && shipping_status == 3 && pay_status == 1) {
        return '已结算';
    } else if (status == 4 && shipping_status == 4 && pay_status == 1) {
        return '已退货';
    } else if (status == 7 && shipping_status == 4 && pay_status == 1) {
        return '已换货';
    } else if (status == 2) {
        return '已取消';
    }
});

new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
});
