<template>
    <div :class="['head','full-width',{'light-bg':header==='fault'}]">
        <div :class="['contain','head', {'light-bg':header==='fault'} ]">
            <ul class="nav clearfix">
                <li class="nav-item fl">
                    <a href="/index.html">
                        <div v-if="logo" :class="['logo',{default:header === 'default'}]" :style="{backgroundImage:`url(${logosync})`}"></div>
                        <div v-else :class="['logo',{default:header === 'default'}]" :style="{backgroundImage:`url(${logoDefault})`}"></div>
                    </a>
                </li>
                <li class="nav-item fl" v-if="header=== 'default'">
                    <router-link to="/">首页</router-link>
                </li>
                <!--<li class="nav-item" v-if="header=== 'default'"><router-link to="/message">消息</router-link></li>-->
            </ul>
            <ul v-if="header=== 'default'" class="fr search-bar clearfix">
                <li class="fl" style="width: 385px;height: 60px;">
                    <input id="search" type="text" v-model="keyword" @keyup.enter="search">
                    <button class="search" @click="search">搜索</button>
                </li>
                <li class="fl">
                    <router-link to="/cart"><i class="cart"></i>购物车</router-link>
                </li>
            </ul>
            <ul v-else class="fr light-search-bar clearfix">
                <li class="fl">
                    <input id="li-search" type="text" v-model="keyword" @keyup.enter="search">
                    <button class="search" @click="search">搜索</button>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    import {mapState} from 'vuex'

    export default {
        name: "head-light",
        data() {
            return {
                keyword:'',
                logo:this.storage.getStorage('business_data').shoplogo1,
                logoDefault:'./logo-combination.png'
            }
        },
        computed: {
            ...mapState('layout', ['header']),
            logosync(){
                if (this.storage.getStorage('business_data')){
                    let $url = this.storage.getStorage('business_data').shoplogo1;
                    if(/^http/.test($url)){
                        return $url.replace('com/n0', 'com/img');
                    }else if(/^\/.{4}\//.test($url)){
                        // /2017/12/25/5a40b39bc9819.jpg
                        return 'http://master.fufentong.com/media/upload'+$url;
                    }else{
                        // /tmp/uploads/20181127/4410c3daa57707828e7e242aa97395fa.png
                        return 'http://mapi.fufentong.com'+$url;
                    }
                }else{
                    return './logo-combination.png'
                }
            }
        },
        methods:{
            search(){
                if (!this.keyword){
                    return
                }
                location.href = `/grade.html?limit=20&order=asc&type=3&page=1&keyword=${this.keyword}&token`
            }
        }
    }
</script>

<style scoped lang="stylus">
    .full-width
        min-width 1000px

    .head
        height 60px
        line-height 60px
        background-color #ff0077
        &.light-bg
            margin-top 50px
            background-color white

    .contain
        width 1000px
        margin 0 auto
        padding 0
        position relative
    ul.nav
        position absolute
        height 60px
    li.nav-item
        display inline-block
        padding-right 30px
        .logo
            width 200px
            height 50px
            margin-top 5px
            background-position 0 100px
            &.default
                background-position 0 50px
        a
            color white
            padding 0 10px
            display block

    ul.search-bar
        position absolute
        right 0
        li
            display inline-block
            margin-left 30px
            position relative
            input
                width 315px
                height 26px
                outline none
                border none
                padding 0 10px
                position absolute
                top 18px
            button.search
                height 26px
                display inline-block
                width 70px
                outline none
                border none
                background-color #f5f5f5
                position absolute
                top 18px
                right 0
            a
                color white
                padding 0 10px
                display block
                i.cart
                    display inline-block
                    width 20px
                    height 20px
                    background-image url("../assets/img/index_icon_shopping_cart.png")
                    vertical-align middle
                    margin-right 6px

    ul.light-search-bar
        position absolute
        right 0
        li
            position relative
            display inline-block
            height 30px
            width 480px
            input
                width 400px
                height 32px
                border 3px solid #ff0077
                outline none
                padding 0 10px
                position absolute
                top 14px
            button
                width 80px
                height 32px
                background-color #ff0077
                color white
                outline none
                border none
                position absolute
                top 14px
                right 0
</style>