<template>
    <div class="recommend" v-if="showRecommend">
        <div class="recommend-head">
            <p class=" bold">根据浏览，猜你喜欢</p>
        </div>
        <div class="row">
            <div :class="['recommend-item', 'fl',{nomargin:(key+1)%4===0}]" v-for="(i,key) in recommend">
                <a :href="`goods.html#/${i.product_id}`" target="_blank">
                    <img class="default-img" :src="i.default_img|img" alt="">
                    <p class="title ac">{{i.product_name}}</p>
                    <p class="price ac bold">{{i|exch(ex)}} <span style="font-size: 14px;font-weight: bold;color: black">{{ex.unit}}</span></p>
                </a>
            </div>
        </div>
    </div>
</template>
<script>
    import {mapState} from 'vuex';

    export default {
        name: "recommend",
        data() {
            return {
                recommend: Array,
                ex: {}
            }
        },
        mounted() {
            if (this.showRecommend){
                this.apiRecommend()
            }
        },
        computed: {
            ...mapState('layout', ['showRecommend'])
        },
        methods: {
            apiRecommend() {
                this.yum('api/ai/dailyRecommendation').then(
                    res => {
                        if (res.data.status === 200) {
                            this.recommend = res.data.data;
                            this.ex = {
                                exchange: res.data.exchange,
                                has_custom_price: res.data.has_custom_price,
                                unit: res.data.unit
                            }
                        }
                    }
                )
            }
        }
    }
</script>

<style scoped lang="stylus">
    .recommend-head
        height 40px
        border-bottom 1px solid #ccc
        padding 10px 20px
        color black

    .row
        background-color #f2f2f2

    .recommend-item
        position relative
        width 200px
        height 260px
        margin-right 7px
        margin-bottom 7px
        background-color white
        a
            color inherit
            display block
        .default-img
            width 160px
            height 160px
            margin 20px 20px 0
        .title
            padding 0 10px
            height 40px
            overflow hidden
        .price
            color #ff0077
            font-size 16px
        &.nomargin
            margin-right 0
</style>