import Vue from 'vue'
import Router from 'vue-router'
import promise from 'es6-promise'

promise.polyfill();
Vue.use(Router);
const index = () => import('./pages/index');
const place = () => import('./pages/address');
const cont = () => import('./pages/cont');
const money = () => import('./pages/money');
const order = () => import('./pages/order');
const safe = () => import('./pages/safe');
const userinfo = () => import('./pages/userinfo');
const exchange = () => import('./pages/money/exchange');
const present = () => import('./pages/money/present');
const setPwd = () => import('./pages/safe/setPwd');
const back = () => import('./pages/order/return');
const backall = () => import('./pages/order/returnGoodsMoney');
const OrderStatus = () => import('./pages/order/OrderStatus');
const Settlement = () => import('./pages/order/Settlement');
const Message = () => import('./pages/message');
const PayTable = () => import('./pages/pay-table');
const Cart = ()=> import('./pages/cart');
const success = ()=> import('./pages/success');
export default new Router({
    mode: 'hash',
    scrollBehavior(to, from, savedPosition) {
        return {x: 0, y: 0}
    },
    routes: [
        {
            path: '/',
            name: 'index',
            component: index
        },
        {
            path: '/address',
            name: 'place',
            component: place
        },
        {
            path: '/success',
            name: 'success',
            component: success
        },
        {
            path: '/cont',
            name: 'cont',
            component: cont
        },
        {
            path: '/money',
            name: 'money',
            component: money
        },
        {
            path: '/money/exchange',
            name: 'exchange',
            component: exchange
        },
        {
            path: '/money/present',
            name: 'present',
            component: present
        },
        {
            path: '/order/return',
            name: 'return',
            component: back
        },
        {
            path:'/order/logistics',
            name:'logistics',
            component:()=> import('./pages/order/logistics')
        },
        {
            path: '/order/returnAll',
            name: 'returnAll',
            component: backall
        },
        {
            path: '/order/detail',
            name: 'OrderStatus',
            component: OrderStatus
        },
        {
            path: '/order',
            name: 'order',
            component: order
        },
        {
            path: '/safe',
            name: 'safe',
            component: safe
        },
        {
            path: '/safe/setpwd',
            name: 'setpwd',
            component: setPwd
        },
        {
            path: '/userinfo',
            name: 'userinfo',
            component: userinfo
        },
        {
            path:'/order/Settlement',
            name:'Settlement',
            component:Settlement
        },
        {
            path:'/message',
            name:'Message',
            component:Message
        },
        {
            path:'/paytable',
            name:'PayTable',
            component:PayTable
        },
        {
            path:'/cart',
            name:'Cart',
            component:Cart
        }
    ]
})