/**
 * 购物车状态
 * 选中的商品
 * */
export const CART = 'CART';
export const LENGTH = 'LENGTH';
export const state = ()=>{
    return {
        shopping_cart:[],
        cart_length:0
    }
}
export const mutations = {
    [CART](state,cart) {
        state.shopping_cart = cart
    },
    [LENGTH](state,length){
        state.cart_length = length
    }
}
export const actions = {
    SET_SHOPPING_CART({commit},cart){
        commit(CART,cart)
    },
    setCartLength({commit},length){
        commit(LENGTH,length)
    }
}