export const LAYOUT = 'LAYOUT';
export const state = () => {
    return {
        showRecommend: true,
        nav: 'default',
        header: 'default'
    }
};
export const mutations = {
    [LAYOUT](state, res) {
        state = Object.assign(state, res)
    }
};
export const actions = {
    Change({commit}, update) {
        commit(LAYOUT, update)
    }
};
