<template>
    <div class="usercenter">
        <f-alert></f-alert>
        <FConfirm></FConfirm>
        <FLoading></FLoading>
        <top :inter="`商城首页`"></top>
        <head-light></head-light>
        <layout></layout>
        <quality v-if="quality"/>
        <footbar v-if="footbar"/>
        <cart-control v-if="cart"></cart-control>
    </div>
</template>

<script>
    import top from '../../components/top.vue'
    import footbar from '../../components/foot'
    import quality from '../../components/quality'
    import HeadLight from './components/head'
    import layout from './components/layout'
    import FAlert from '../../components/FAlert'
    import FConfirm from '../../components/FConfirm'
    import CartControl from './components/cart-control'
    import FLoading from '../../components/FLoading'
    export default {
        name: 'usercenter',
        metaInfo: {
            title: '福分通-用户中心', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        data() {
            return {
                quality:true,
                footbar:true,
                cart:false
            }
        },
        mounted(){
            this.bus.$on('main-page-layout',function (q,f,c) {
                this.quality = q;
                this.footbar = f;
                this.cart = c
            }.bind(this))
        },
        components: {
            top, footbar, quality, HeadLight, layout, FAlert,CartControl,FConfirm,FLoading
        }
    }
</script>

<style>

    .dpkg {
        display: block;
        width: 400px;
        margin: 100px auto;
    }

</style>
